.about {
  width: 78vw;
  height: 100vh;
  margin: 3vh 0 9vh 22vw;
  background-color: white;
  display: flex;
  justify-content: space-between;
}
.about1 {
  width: 72vw;
  height: 94vh;
  margin: 5vh 5vw;
  background-color: white;
}
.about1 > p {
  font-size: 1.1rem;
  font-family: "Girassol", cursive;
  letter-spacing: 1px;
}
.about1 > h1 {
  font-size: 2.5rem;
  border-bottom: 4px solid black;
  font-family: "Zen Dots", cursive;
  letter-spacing: 2px;
}

.about3 {
  display: flex;
  justify-content: space-evenly;
  font-family: "Girassol", cursive;
  letter-spacing: 0.6px;
}
.about3 > img {
  width: 300px;
  height: 300px;
  margin: 1rem 3rem 0 0;
}

.about4 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-size: 1rem;
}
.about4 h3 {
  margin: 1rem;
  font-weight: 400;
}
span {
  font-weight: 600;
}
@media only screen and (max-width: 768px) {
  .about {
    width: 100vw;
    height: 140vh;
    margin: 0;
    padding: 0;
    background-color: white;
    display: flex;
    justify-content: space-between;
  }
  .about1 {
    width: 100vw;
    height: 140vh;
  }
  .about1 > p {
    font-size: 1rem;
    text-align: center;
  }
  .about3 > div > p {
    font-size: 1rem;
    text-align: center;
  }
  .about1 > h1 {
    text-align: center;
  }
  .about-title {
    font-size: 1rem;
    text-align: center;
  }
  .about3 {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
  .about3 > img {
    width: 300px;
    height: 300px;
    margin: 1rem auto;
    border-radius: 2rem;
  }
  .about4 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    font-size: 1rem;
  }
  .about4 h3 {
    margin: 1rem;
    font-weight: 400;
  }
  span {
    font-weight: 600;
  }
}
