nav {
  position: fixed;
  height: 100vh;
  width: 22vw;
  background-color: rgb(4, 11, 20);
  margin: 0;
  z-index: 10000;
}
.mobile {
  display: none;
}
.hero {
  margin-top: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero > img {
  border: 8px solid rgb(44, 47, 63);
  width: 105px;
  height: 105px;
  border-radius: 50%;
}
.hero-text {
  letter-spacing: 3px;
  font-size: 1.5rem;
  text-align: center;
  color: white;
  padding-bottom: 1rem;
  font-family: "Zen Dots", cursive;
}
.social {
  display: flex;
  justify-content: space-evenly;
}
.icons {
  width: 45px;
  height: 45px;
  background-color: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.icons > img {
  width: 35px;
  height: 35px;
}
.icons:hover {
  animation: pulse 1s;
  background-color: #27dfa1;
}
.nav-list > li > a:hover {
  padding-bottom: 4px;
  color: red;
  border-bottom: 3px solid blueviolet;
}

.nav-list {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 50vh;
}
.nav-list li {
  list-style: none;
}
.nav-list > li > a {
  text-decoration: none;
  letter-spacing: 1px;
  color: white;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  font-family: "Girassol", cursive;
  letter-spacing: 2px;
}

@media only screen and (max-width: 768px) {
  nav {
    position: fixed;
    height: 100vh;
    width: 70vw;
    padding-top: 2rem;
    background-color: rgb(4, 11, 20);
  }
  .active {
    display: none;
  }
  .social .icons {
    width: 30px;
    height: 30px;
    background-color: white;
    border-radius: 50%;
  }
  .mobile {
    position: fixed;
    z-index: 10000;
    visibility: visible;
    display: block !important;
    top: 1rem;
    right: 1rem;
  }
  .icons {
    width: 50px !important;
    height: 50px !important;
    background-color: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .icons > img {
    width: 45px;
    height: 45px;
    padding: 1rem;
  }
}
