.contact {
  width: 78vw;
  height: 50vh;
  margin: 2vh 0 0 22vw;
  background-color: black;
  display: flex;
  justify-content: space-evenly;
}

.contact > form {
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
}
.form > h2 {
  font-size: 2rem;
  color: white;
  margin: 0.5rem 0.5rem 0 0.5rem;
  border-bottom: 2px solid red;
  padding-bottom: 0.5rem;
}
.form > p {
  font-size: 2rem;
  color: white;
  margin: 0.5rem;
}
.form > p > input {
  font-size: 1.1rem;
}
.form > button {
  font-size: 1.2rem;
  margin: 1rem;
  cursor: pointer;
}
.links {
  margin: 4rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: space-between;
}
.links > a {
  text-align: center;
  color: white;
  margin: 0.5rem;
  text-decoration: none;
  padding-bottom: 4px;
}

.links :nth-child(1) {
  margin: 0;
  color: white;
  padding: 1rem;
  border-bottom: 2px solid white;
  text-align: center;
  font-size: 1.5rem;
}
.links > a:hover {
  color: red;
  border-bottom: 1px solid blueviolet;
}

@media only screen and (max-width: 768px) {
  .contact {
    width: 100vw;
    height: auto;
    margin: 0;
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  .contact > form {
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
  }
  .form > h2 {
    font-size: 2rem;
    color: white;
    margin: 0.5rem 0.5rem 0 0.5rem;
    border-bottom: 2px solid red;
    padding-bottom: 0.5rem;
  }
  .form > p {
    font-size: 2rem;
    color: white;
    margin: 0.5rem;
  }
  .form > p > input {
    font-size: 1.1rem;
  }
  .form > button {
    font-size: 1.2rem;
    margin: 1rem;
    cursor: pointer;
  }
  .links {
    margin: 1rem 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-content: space-around;
  }
  .links > a {
    text-align: center;
    color: white;
    margin: 0.5rem;
    text-decoration: none;
    padding-bottom: 4px;
  }

  .links :nth-child(1) {
    margin: 0;
    color: white;
    padding: 1.5rem;
    border-bottom: 2px solid white;
    text-align: center;
    font-size: 1.5rem;
  }
  .links > a:hover {
    color: red;
    border-bottom: 1px solid blueviolet;
  }
}
