.SkillsSection {
  width: 78vw;
  height: auto;
  margin: 12vh 0 0 22vw;
  position: relative;
  background-color: rgb(9, 30, 56);
}
.particles {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.skill-icons {
  margin: 1vh 5vw;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.skill-text > h1 {
  position: relative;
  margin: 5vh 5vw 3vh 5vw;
  padding-top: 4vh;
  font-size: 2.5rem;
  font-weight: bold;
  color: white;
  font-family: "Zen Dots", cursive;
  letter-spacing: 2px;
  border-bottom: 4px solid white;
  z-index: 1000;
}
.size {
  width: 8rem;
  height: 8rem;
  margin: 1rem;
  border: 2px solid black;
  padding: 4px;
  border-radius: 2rem;
  background-color: white;
  z-index: 100;
}

.size:hover {
  animation: pulse 1s;
  background-color: #f012be;
}

@media only screen and (max-width: 768px) {
  .SkillsSection {
    width: 100vw;
    height: 100vh;
    margin: 0;
  }
  .skill-icons {
    margin: 1vh 5vw;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
  }
  .skill-text > h1 {
    margin: 5vh 5vw 3vh 5vw;
    font-size: 2.5rem;
    text-align: center;
  }
  .size {
    padding: 3px;
    width: 5rem;
    height: 5rem;
    margin: 1rem;
  }
}
