.home-text {
  color: black;
  font-size: 4rem;
  margin-bottom: -1.5rem;
  color: white;
}
.home-para {
  color: black;
  font-size: 2.2rem;
  color: white;
}
.hero-container {
  position: absolute;
  z-index: 2;
  top: 28%;
  left: 15%;
  letter-spacing: 1px;
}
.home {
  margin: 0 0 0 22vw;
  position: relative;
  background-size: cover;
  object-fit: cover;
  z-index: 100;
  user-select: none;
}

.typed {
  border-bottom: 3px solid rgb(20, 157, 221);
}
.typed-cursor {
  opacity: 1;
  animation: typedjsBlink 0.7s infinite;
  -webkit-animation: typedjsBlink 0.7s infinite;
  animation: typedjsBlink 0.7s infinite;
}

@keyframes typedjsBlink {
  50% {
    opacity: 0;
  }
}

@-webkit-keyframes typedjsBlink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.typed-fade-out {
  opacity: 0;
  transition: opacity 0.25s;
  -webkit-animation: 0;
  animation: 0;
}
@media only screen and (max-width: 768px) {
  .home {
    width: 100vw;
    height: 100vh;
    margin: 0;
    background-color: white;
    display: flex;
    justify-content: space-between;
  }
  .hero-container {
    position: absolute;
    z-index: 2;
    top: 35%;
    left: 10%;
  }
  .home-text {
    color: black;
    font-size: 3rem;
    margin-bottom: 0rem;
    color: white;
  }
  .home-para {
    color: black;
    font-size: 1.5rem;
    color: white;
  }
  .home-para,
  .home-text {
    margin: 2rem 0;
    letter-spacing: 2px;
  }
}
