#portfolio {
  width: 78vw;
  height: auto;
  margin: 0 0 0 22vw;
  display: flex;
  justify-content: space-between;
}
.portfolio1 {
  width: 72vw;
  height: auto;
  margin: 5vh 5vw;
  background-color: white;
}
.portfolio2 {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
.portfolio1 > h1 {
  font-size: 2.5rem;
  margin-bottom: 3rem;
  border-bottom: 4px solid black;
  font-family: "Zen Dots", cursive;
  letter-spacing: 2px;
}

.card > img {
  width: 25vw !important;
  height: 25vw !important;
  object-fit: cover;
  background-size: cover;
  border-radius: 3rem;
  border: 2px solid red;
}

.card > div > h4 {
  margin: 0.8rem auto 2rem auto;
  text-align: center;
  font-size: 1.3rem;
  text-decoration: underline;
  padding: 0 1rem 1rem 1rem;
}

.card {
  position: relative;
  width: 50%;
  text-align: center;
  font-family: "Zen Dots", cursive;
}

.image {
  opacity: 1;
  transition: 0.5s ease;
  backface-visibility: hidden;
}

.card-text {
  transition: 0.5s ease;
  opacity: 0;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.card:hover .image {
  opacity: 0.3;
}

.card:hover .card-text {
  opacity: 1;
}

.text > a {
  text-decoration: none;
  color: black;
  font-size: 3rem;
  font-weight: bold;
  letter-spacing: 2px;
}

@media only screen and (max-width: 768px) {
  .portfolio {
    width: 100vw;
    height: auto;
    margin: 0 !important;
    padding: 0 !important;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  .portfolio1 {
    margin: 0 -2rem;
    font-size: 2.5rem;
    text-align: center;
  }
  .portfolio1 > h1 {
    text-align: center;
  }

  .card > img {
    width: 70vw !important;
    height: 70vw !important;
    object-fit: cover;
    background-size: cover;
    border-radius: 3rem;
    border: 2px solid red;
  }

  .card > div > h4 {
    margin: 0.8rem auto 2rem auto;
    text-align: center;
    font-size: 1.3rem;
    text-decoration: underline;
    padding: 0 1rem 1rem 1rem;
  }

  .card {
    position: relative;
    width: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }

  .image {
    opacity: 1;
    transition: 0.5s ease;
    backface-visibility: hidden;
  }

  .card-text {
    transition: 0.5s ease;
    opacity: 0;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }

  .card:hover .image {
    opacity: 0.3;
  }

  .card:hover .card-text {
    opacity: 1;
  }

  .text > a {
    text-decoration: none;
    color: black;
    font-size: 3rem;
    font-weight: bold;
    letter-spacing: 2px;
  }
}
